<template>
	<view class="goods-item">
		<radio @click="radioClickHandler" :checked="goods.goods_state" color="blue" v-if="showRadio"/>
		<view class="goods-item-left">
			<image :src="goods.goods_small_logo || defaultPic" style="width: 100px; height: 100px" class="goods-pic" mode=""></image>
		</view>
		<view class="goods-item-right">
			<!-- title -->
			<view class="goods-name">
				{{ goods.goods_name }}
			</view>
			<!-- price -->
			<view class="goods-info-box">
				<view class="goods-price">
					{{ goods.goods_price | tofixed }}
				</view>
				<uni-number-box v-if="showNum" :min="1" :value="goods.goods_count" @change="numberChangeHandler"></uni-number-box>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	name: 'my-goods',
	props: {
		goods: {
			type: Object,
			default: {}
		},
		showRadio:{
			type:Boolean,
			default:false
		},
		showNum:{
			type:Boolean,
			default:false
		}
	},
	data() {
		return {
			defaultPic: 'https://img3.doubanio.com/f/movie/8dd0c794499fe925ae2ae89ee30cd225750457b4/pics/movie/celebrity-default-medium.png'
		};
	},
	methods:{
		radioClickHandler(){
			this.$emit('radio-change',{
				goods_id:this.goods.goods_id,
				goods_state:!this.goods.goods_state
			})
		},
		numberChangeHandler(val){
			this.$emit('num-change',{
				goods_id:this.goods.goods_id,
				goods_count:+val
			})
		}
		
	},
	//过滤器	
	filters:{
		tofixed(num){
			return Number(num).toFixed(2)
		}
	}
};
</script>

<style lang="scss">
.goods-item {
	width: 750rpx;
	box-sizing: border-box;
	
	display: flex;
	padding: 10px 5px;
	border-bottom: 1px solid #f0f0f0;

	.goods-item-left {
		margin-right: 5px;
		display: flex;
		justify-content: space-around;
		align-items: center;
		.goods-pic {
			width: 100px;
			height: 100px;
			display: block;
		}
	}

	.goods-item-right {
		display: flex;
		flex: 1;
		flex-direction: column;
		justify-content: space-between;

		.goods-name {
			font-size: 13px;
		}
		.goods-info-box{
			display: flex;
			align-items: center;
			justify-content: space-around;
		}

		.goods-price {
			font-size: 16px;
			color: #c00000;
		}
	}
}
</style>
